@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:wght@600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=M+PLUS+Rounded+1c&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes slide-in {
  0% {
    height: 0;
  }
  100% {
    height: 75vh;
  }
}

@keyframes slide-in {
  0% {
    height: 0;
  }
  100% {
    height: 75vh;
  }
}

@-webkit-keyframes slide-out {
  0% {
    padding-top: 10px;
    padding-bottom: 5px;
    height: 75vh;
  }
  100% {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
  }
}

@keyframes slide-out {
  0% {
    padding-top: 10px;
    padding-bottom: 5px;
    height: 75vh;
  }
  100% {
    padding-top: 0;
    padding-bottom: 0;
    height: 0;
  }
}

@-webkit-keyframes to-long {
  0% {
    -webkit-transform: scaleY(0.2);
            transform: scaleY(0.2);
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@keyframes to-long {
  0% {
    -webkit-transform: scaleY(0.2);
            transform: scaleY(0.2);
  }
  100% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
}

@-webkit-keyframes to-short {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  100% {
    -webkit-transform: scaleY(0.2);
            transform: scaleY(0.2);
  }
}

@keyframes to-short {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
  }
  100% {
    -webkit-transform: scaleY(0.2);
            transform: scaleY(0.2);
  }
}

@-webkit-keyframes flash {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}
@keyframes flash {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.btn {
  margin: 6px;
  width: 13em;
  font-size: 30px;
  line-height: 1.42;
  padding: 0.6em 1.6em;
  font-weight: normal;
  border: none;
  background-color: white;
  border-radius: 1.46em;
  cursor: pointer;
  vertical-align: bottom;
  transition: background-color 0.3s, color 0.3s;
  font-family: "M PLUS Rounded 1c", sans-serif;
}
.btn.dark {
  background-color: #d8d8d8;
}
.btn.dark:hover {
  background-color: gray;
}
.btn:hover {
  background-color: #dff7df;
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
  transition: background-color 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
}
.btn:active {
  color: black;
}

h1 {
  display: inline-block;
  position: relative;
  margin-top: 0;
}
h1:before {
  content: "";
  position: absolute;
  left: 50%;
  bottom: -10px; /*下線の上下位置調整*/
  display: inline-block;
  width: 2em; /*下線の幅*/
  height: 2px; /*下線の太さ*/
  -webkit-transform: translateX(-50%);
  transform: translate(-50%); /*位置調整*/
  background-color: #ffffff; /*下線の色*/
}

